body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans";
}

#root, body, html {
  height: 100%;
}

/* Fix issue where sweetalert2 breaks layout */
body.swal2-height-auto.swal2-height-auto.swal2-height-auto {
  height: 100% !important;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* remove default user agent style */
a {
  text-decoration: none;
  color: inherit;
}


